<template>
  <nav class="navbar" role="navigation" aria-label="main navigation">
    <div class="navbar-brand">
      <a class="navbar-item">
        <img src="../assets/logo.png" />
      </a>

      <a
        role="button"
        class="navbar-burger"
        aria-label="menu"
        aria-expanded="false"
        data-target="navbarBasicExample"
      >
        <span aria-hidden="true"></span>
        <span aria-hidden="true"></span>
        <span aria-hidden="true"></span>
      </a>
    </div>

    <div id="navbarBasicExample" class="navbar-menu">
      <div class="navbar-start">
        <a class="navbar-item" href="#"> Inicio </a>
        <div class="navbar-item has-dropdown is-hoverable">
          <a class="navbar-link is-arrowless"> Acciones </a>

          <div class="navbar-dropdown">a</div>
        </div>
      </div>

      <div class="navbar-end">
        <div class="navbar-item">
          <div class="buttons">
            <a class="button is-primary">
              <strong>Buscar</strong>
            </a>
          </div>
        </div>
        <div class="navbar-item">
          <input class="input" type="text" placeholder="busqueda..." />
        </div>
      </div>
    </div>
  </nav>

  <table class="table my-4 mx-4 is-justified-content-center">
    <thead>
      <tr>
        <th>Nombre del Animal</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(animal, index) in data" :key="index">
        <td>{{ animal }}</td>
      </tr>
    </tbody>
  </table>
  <div class="control">
    <input
      class="input"
      type="text"
      id="animal"
      placeholder="escribe un animal"
    />
    <button class="button is-primary" type="button" id="submit">Submit</button>
  </div>
  <div id="alert"></div>
</template>

<script>
import axios from "axios";

export default {
  name: "myNavbar",
  data() {
    return {
      data: [],
    };
  },
  mounted() {
    axios
      .get("https://backend.master-vm.shop/api/animales")
      .then((response) => {
        this.data = response.data;
        console.log(this.data);
      })
      .catch((error) => {
        console.error("Error al obtener los animales:", error);
      });
    const submitButton = document.getElementById("submit");
    submitButton.addEventListener("click", () => {
      axios
        .post(
          "https://backend.master-vm.shop/api/animales",
          {
            animal: document.getElementById("animal").value,
          },
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
            },
          }
        )
        .then((result) => {
          if (result) {
            document.getElementById("alert").innerHTML = `
            <div class="icon-text">
              <span class="icon has-text-success">
                <i class="fas fa-check-square"></i>
              </span>
              <span>Success</span>
            </div>

            <p class="block">
              registro enviado
            </p>
            `;
          } else {
            document.getElementById("alert").innerHTML = `
            <div class="icon-text">
              <span class="icon has-text-warning">
                <i class="fas fa-exclamation-triangle"></i>
              </span>
              <span>Warning</span>
            </div>

            <p class="block">
              error al crear el registro
            </p>
            `;
          }
        });
    });
  },
};
</script>

